  #quote i {
    float: right;
  }
  #convo {
    padding-top: 0px;
    padding-bottom: 0px;
  }
  #convo .left {
    width: 60%;
    float: left;
  }
  #convo .left img {
    display: block;
    max-width: 100%;
    height: auto;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;
    float: right;
    padding-top: 25px;
  }
  #convo .right {
    float: right;
    width: 40%;
  }
  #convo .right img {
    display: block;
    max-width: 100%;
    height: auto;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;
    float: left;
  }
}

